.container-gren {
    display: flex;
    flex-direction: column;
    background-color: #4fbdc0;
}
.container-gren h1 {
    width: 100%;
    display: block;
    text-align: center;
    background-color: #369fa2;
    padding: 30px 0;
    color: #fff;
    font-size: 2rem;
    font-weight: normal;
    position: relative;
}
.container-gren h1::after {
    content: "";
    position: absolute;
    width: 30px;
    height: 4px;
    background-color: #65d6da;
    bottom: 0;
    left: 50%;
    top: 78%;
    border-radius: 8px;
}

.container-items-gren {
    display: flex;
    width: 100%;
}
.container-cadastro-gren {
    width: 50%;
    background-color: #fff;
    margin-left: 0px;
    padding-top: 50px;
    padding-left: 20%;
}
.container-cadastro-gren button {
    width: max-content;
    display: flex;
    align-items: center;
    margin-bottom: 200px;
    background-color: #237d80;
    box-shadow: 4px 4px 10px #237d80;
}
.container-cadastro-gren button:hover {
    background-color: #000;
    box-shadow: 4px 4px 10px #333;
}

.form-items-gren label {
    display: block;
    margin-bottom: 5px;
}
.form-items-gren input {
    width: 90%;
    margin-bottom: 10px;
}
.form-items-gren input:hover {
    border: 1px solid #4fbdc0;
    box-shadow: 1px 1px 4px #4fbdc0;
}
.form-items-gren input:focus {
    border: 1px solid #4fbdc0;
    box-shadow: 1px 1px 4px #4fbdc0;
}

/* planos */

.container-plano-gren {
    background-color: #349ea2;
    width: 400px;
    height: 250px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    margin: 20px;
    border-radius: 4px 4px 0 0;
    border: 1px solid #60d4d8;
    box-shadow: 1px 4px 10px #287779;
    border-bottom: none;
}
.container-plano-principal-gren p {
    margin: 20px;
    color: #fff;
    font-size: 1.5rem;
}
.container-plano-principal-gren h4 {
    background-color: #237d80;
    width: 91%;
    margin: 20px;
    margin-top: -20px !important;
    padding: 20px 0px;
    border-radius: 0 0 4px 4px;
    color: #fff;
    text-align: center;
    font-weight: normal;
    font-size: 2rem;
    border: 1px solid #60d4d8;
    box-shadow: 1px 4px 10px #287779;
    border-top: none;
}

.container-plano-gren h3 {
    font-size: 3rem;
    font-weight: 500;
    color: #fff;
    padding: 0px 20px;
    margin-bottom: 20px;
}
.container-plano-gren span {
    color: #fff;
    display: block;
    margin: 0 20px;
    width: 90%;
    display: flex;
    justify-content: space-between;
}
.container-plano-gren span:hover {
    cursor: pointer;
}
.container-plano-gren div {
    position: relative;
    padding: 5px;
}
.container-plano-gren div:nth-child(even) {
    width: 100%;
    background-color: #2a7679;
    border-radius: 4px;
}
.container-plano-gren div::before {
    content: "";
    width: 10px;
    height: 4px;
    background: linear-gradient(90deg, #fff, #2a7679);
    position: absolute;
    left: 4px;
    top: 10px;
}
.container-plano-gren div:last-child:before {
    background: linear-gradient(90deg, rgb(100, 5, 5), rgb(222, 61, 61));
}
.container-plano-gren p {
    color: #fff;
    font-size: 14px;
    font-style: italic;
    padding: 0px;
    margin: 0px 20px;
    margin-bottom: 10px;
}
.no-rotate {
    transition: 0.5s;
    transform: rotate(0);
}
.rotate {
    transition: 0.5s;
    transform: rotate(180deg);
}

.dados {
    font-size: 2rem;
    margin-bottom: 10px;
}

.pagamento-div-green {
    width: 90%;
    border-top: 1px solid #65b8bb;
    padding-top: 20px;
}
.pagamento-div-green p {
    margin-bottom: 20px;
    position: relative;
    font-size: 1.5rem;
    color: #333;
}
.pagamento-div-green p::before {
    content: "2";
    position: absolute;
    left: -20px;
}
